import React from 'react';
import Card from './Card';

const Cardlist = ({robots}) => {
	return(
		<>
			{
				robots.map((user,i) => {
					return (
						<Card 
							key={i} 
							id={robots[i].id} 
							name={robots[i].name} 
							username={robots[i].username} 
							email={robots[i].email}
							/>
						);
				})
			}
		</>
	);
}

export default Cardlist;